import React, { Component } from 'react';
import './LectureContent.css';
import { Link } from 'react-router-dom';
import { Activity } from '../api'

const pathPrefix = window._config.pathPrefix;

const img_1 = '/static/activity/baoming_yue_1.png';
const img_2 = '/static/activity/baoming_yue_2.png';
const toast = [<p key="1">13141124:00</p>,
               <p key="2">2</p>,
               <p key="3">31518</p>,
               <p key="4">4</p>,
               <p key="5">5</p>];
const toast2 = [<p key="1"> </p>,
                <p key="2">  </p>,
                <p key="3"> 1.</p>,
                <p key="4"> 2.</p>,
                <p key="5"> 3.</p>,
                <p key="6">   </p>,
                <p key="7"> 1.</p>,
                <p key="8"> 2.</p>,
                <p key="9"> 3.</p>,
                <p key="10"> 4.</p>,
                <p key="11"> 5. </p>,
                <p key="12">   </p>,
                <p key="13"> </p>,
                <p key="14"> 18</p>];



class LectureContent extends Component {

  constructor(props){
      super(props);

      this.state = {
          result:{},
          click_Baoming: false,
          is_read: false,
          is_next: false,
          makeReservation: true,
        };
  }

  timestampToTime = (timestamp) => {
        let date = new Date(timestamp);//10*1000131000
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
        let h = date.getHours() + ':';
        let m = date.getMinutes();
        // let s = date.getSeconds();
        let week = this.getweekDay(new Date(Y+M+D));
        let time = (h<=12) ? "" : "";
        return Y+M+D+"("+week+") "+time+h+m;
  }

  getweekDay = (date) => {
    let week;
    if(date.getDay()===0) week=""
    if(date.getDay()===1) week=""
    if(date.getDay()===2) week=""
    if(date.getDay()===3) week=""
    if(date.getDay()===4) week=""
    if(date.getDay()===5) week=""
    if(date.getDay()===6) week=""
    return week;
  }

  //
  async componentDidMount(){
    try{
      let data = await Activity.activityContent({
        _id : this.props.location.state.id,
      });

      //console.log(data);
      if (data.data.reservation && data.data.reservation.limit > 0) {
        if (data.data.reservation.count&&data.data.reservation.count>data.data.reservation.limit) {
          this.setState({
            makeReservation: false,
          })
        }
      }
      this.setState({
        result: data.data,
      })
    }catch(e){
      //
      //console.log(e);
    }
  }

  baoming = (value) => {
    if (value === "") {
      this.setState({
        click_Baoming: true,
      })
    }
  }


  cancel_baoming = () => {
    this.setState({
      click_Baoming: false,
      is_read: false,
      is_next: false,
    })
  }

  read = () => {
    this.setState({
      is_read: !this.state.is_read,
    })
  }

  next = () => {
    if (this.state.is_read) {
      this.setState({
        is_next: true,
      })
    }
  }

  render() {
    //console.log(this.state.result);
    let data = this.state.result || {};

    let pathname = `${pathPrefix}/signUp/${data._id}`;
    let LinkData = {
      id: data._id,
      model:"lecture"
    }
    var path = {
     pathname:pathname,
     state:LinkData,
   }

    return(
      <div className="lecture-content-home">
        <div className="activity-toast" style={{display:`${this.state.click_Baoming ? "block" : "none"}`}}>
          <div className="activity-toast-div">
            <div className="activity-toast-div-title">{this.state.is_next?"":""}</div>
            <div className="activity-toast-div-line"></div>
            <div className="activity-toast-div-content">{this.state.is_next?toast2:toast}</div>
            {(this.state.is_next) ? (
              <div className="activity-toast-div-footer">
                <div className="activity-toast-div-footer-next-noagree" onClick={this.cancel_baoming}></div>
                <Link to={path}>
                  <div className="activity-toast-div-footer-next-agree"></div>
                </Link>
              </div>
            ) : (
              <div className="activity-toast-div-footer">
                <div className="activity-toast-div-footer-yiyue" onClick={this.read} style={{background: `url(${this.state.is_read?img_2:img_1})   no-repeat  center / 100% 100%`, display:`${this.state.is_next ? "none" : "block"}`}}></div>
                <div className={`${this.state.is_read?"activity-toast-div-footer-next-isread":"activity-toast-div-footer-next-noread"} + " activity-toast-div-footer-next-isread"`} onClick={this.next} style={{display:`${this.state.is_next ? "none" : "block"}`}}></div>
              </div>
              )}
          </div>
          <div className="activity-toast-cancelbutton" onClick={this.cancel_baoming}></div>
        </div>
        <div className="lecture-content-title">{data.title}</div>
        <div className="lecture-jiangzuoxq-bg">
          <div className="lecture-content-top">
            <div className="lecture-content-info">
                <div className="lecture-content-info-flex">{data.speaker}</div>
                <div className="lecture-content-info-flex"><div style={{height:"28px", width:"73px"}}></div><div style={{width:"calc(100% - 62px)"}}>{data.time ? this.timestampToTime(data.time.start): ''}</div></div>
                <div className="lecture-content-info-flex"><div style={{height:"28px", width:"73px"}}></div><div style={{width:"calc(100% - 62px)"}}>{data.address}</div></div>
                <div className="lecture-content-info-flex">{data.limit ? (data.limit.age.min + ' - ' +data.limit.age.max) : ""}</div>
                <div className="lecture-content-info-flex" style={{color:`${this.state.makeReservation ?  '#75ABED' : '#999999'}`}}>{this.state.makeReservation ? '' : ''}{data.reservation&&data.reservation.limit>0 ? ((data.reservation.count ? data.reservation.count : '0') + '/' +data.reservation.limit) : ""}</div>
            </div>
          </div>
          <div className="lecture-content-cover" style={{background: `url(${data.cover}) center no-repeat / cover`}}></div>
        </div>
        <div className="lecture-content-digest" dangerouslySetInnerHTML={{__html: data.content}}/>
        <div className="lecture-footer">
          <div className="bg-cc footer-image"></div>
        </div>
        <div className={`${data.time ? (data.time.start - (new Date().getTime()) < 0) ? "lecture-footer-jieshu": (data.reservation.count ?  (data.reservation.count >= data.reservation.limit ? "lecture-footer-man":"lecture-footer-baoming") : "lecture-footer-baoming"): ''}`} onClick={(e) => this.baoming(e.target.innerHTML)}>
        {data.time ? (data.time.start - (new Date().getTime()) < 0) ? "": (data.reservation.count ?  (data.reservation.count >= data.reservation.limit ? "":"") : ""): ''}
        </div>
      </div>
    );
  }
}

export default LectureContent;



