import React, { Component } from 'react';
import './ActivityContent.css';
import { Link } from 'react-router-dom';
import { Activity } from '../api'

const pathPrefix = window._config.pathPrefix;

const img_1 = '/static/activity/baoming_yue_1.png';
const img_2 = '/static/activity/baoming_yue_2.png';
const btn_play = '/static/activity/btn_play1.png';
const btn_stop = '/static/activity/btn_stop1.png';

const toast = [<p key="1">    13141124:00</p>,
               <p key="2">    2</p>,
               <p key="3">    31518</p>,
               <p key="4">    4</p>,
               <p key="5">    5</p>];
const toast2 = [<p key="1"> </p>,
                <p key="2">  </p>,
                <p key="3"> 1.</p>,
                <p key="4"> 2.</p>,
                <p key="5"> 3.</p>,
                <p key="6">   </p>,
                <p key="7"> 1.</p>,
                <p key="8"> 2.</p>,
                <p key="9"> 3.</p>,
                <p key="10"> 4.</p>,
                <p key="11"> 5. </p>,
                <p key="12">   </p>,
                <p key="13"> </p>,
                <p key="14"> 18</p>];

const grades = ['','','','','','','','','','','','','','',''];

class ActivityContent extends Component {

    constructor(props){
        super(props);

        this.state = {
          result:{},
          click_Baoming: false,
          is_read: false,
          is_next: false,
          makeReservation: true,
          videoIsPause: true,
          audioIsPause: true,
          };
    }
    timestampToTime = (timestamp) => {
          let date = new Date(timestamp);//10*1000131000
          let Y = date.getFullYear() + '-';
          let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
          let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
          let h = date.getHours() + ':';
          let m = date.getMinutes();
          // let s = date.getSeconds();
          let week = this.getweekDay(new Date(Y+M+D));
          let time = (h<=12) ? "" : "";
          return Y+M+D+"("+week+") "+time+h+m;
    }

    getweekDay = (date) => {
      let week;
      if(date.getDay()===0) week=""
      if(date.getDay()===1) week=""
      if(date.getDay()===2) week=""
      if(date.getDay()===3) week=""
      if(date.getDay()===4) week=""
      if(date.getDay()===5) week=""
      if(date.getDay()===6) week=""
      return week;
    }

    getGrade = (value) => {
      return grades[value];
    }

    //
    async componentDidMount(){
      try{
        let data = await Activity.activityContent({
          _id : this.props.location.state.id,
        });
        if (data.data.reservation && data.data.reservation.limit > 0) {
          if (data.data.reservation.count&&data.data.reservation.count>data.data.reservation.limit) {
            this.setState({
              makeReservation: false,
            })
          }
        }
        this.setState({
          result: data.data,
        })
      }catch(e){
        //
      }
    }

    baoming = (value) => {
      if (value === "") {
        this.setState({
          click_Baoming: true,
        })
      }
    }


    cancel_baoming = () => {
      this.setState({
        click_Baoming: false,
        is_read: false,
        is_next: false,
      })
    }

    read = () => {
      this.setState({
        is_read: !this.state.is_read,
      })
    }

    next = () => {
      if (this.state.is_read) {
        this.setState({
          is_next: true,
        })
      }
    }
  render() {
    let data = this.state.result || {};

    let pathname = `${pathPrefix}/signUp/${data._id}`;
    let LinkData = {
      id: data._id,
      model:"education"
    }
    var path = {
     pathname:pathname,
     state:LinkData,
   }

    return(
      <div className="activity-content-home">
        <div className="activity-toast" style={{display:`${this.state.click_Baoming ? "block" : "none"}`}}>
          <div className="activity-toast-div">
            <div className="activity-toast-div-title">{this.state.is_next?"":""}</div>
            <div className="activity-toast-div-line"></div>
            <div className="activity-toast-div-content">{this.state.is_next?toast2:toast}</div>
            {(this.state.is_next) ? (
              <div className="activity-toast-div-footer">
                <div className="activity-toast-div-footer-next-noagree" onClick={this.cancel_baoming}></div>
                <Link to={path}>
                  <div className="activity-toast-div-footer-next-agree"></div>
                </Link>
              </div>
            ) : (
              <div className="activity-toast-div-footer">
                <div className="activity-toast-div-footer-yiyue" onClick={this.read} style={{background: `url(${this.state.is_read?img_2:img_1})   no-repeat  center / 100% 100%`, display:`${this.state.is_next ? "none" : "block"}`}}></div>
                <div className={`${this.state.is_read?"activity-toast-div-footer-next-isread":"activity-toast-div-footer-next-noread"} + " activity-toast-div-footer-next-isread"`} onClick={this.next} style={{display:`${this.state.is_next ? "none" : "block"}`}}></div>
              </div>
              )}
          </div>
          <div className="activity-toast-cancelbutton" onClick={this.cancel_baoming}></div>
        </div>
        <div className="activity-content-title">{data.title}</div>
        <div className="line"></div>
        <div className="activity-content-info">
          <div>{data.time ? this.timestampToTime(data.time.start): ''}</div>
          <div>{data.address}</div>
          <div>{data.limit? ((data.limit.type === 'age') ? (data.limit.age.min + "~" + data.limit.age.max + "" + data.reservation.limit + "") : (this.getGrade(data.limit.grade.min) + "~" + this.getGrade(data.limit.grade.max) + "" + data.reservation.limit + "")) : ""}</div>
          <div>{data.cost}</div>
          <div style={{color:`${this.state.makeReservation ?  '#75ABED' : '#999999'}`}}>{this.state.makeReservation ? '' : ''}{data.reservation&&data.reservation.limit>0 ? ((data.reservation.count ? data.reservation.count : '0') + '/' +data.reservation.limit) : ""}</div>
        </div>
        <div className="activity-content-cover" style={{background: `url(${data.cover}) center no-repeat / cover`}}></div>
        <div className="activity-content-digest">{data.intro}</div>
        {data.videos && data.videos.length > 0? (<div className="activity-content-video-div">

          <video ref={ref=>this.video=ref} className="activity-content-video" src={`${data.videos?data.videos[0].src : ""}`} onClick={()=>{
            this.video.pause();
            this.setState({
              videoIsPause:true
            })
          }}
          onEnded={() => {
            this.setState({
              videoIsPause:true
            })
          }}></video>
          <div className="activity-content-video-mengban" style={{display:`${this.state.videoIsPause ? "block" : "none"}`}}>
            <div className="activity-content-video-mengban-button" onClick={()=>{
              this.video.play();
              this.setState({
                videoIsPause:false
              })
            }}></div>
          </div>
          <div className="activity-content-videotitle" >{data.videos?data.videos[0].title : ""}</div>
        </div>) : null}

        {data.audios && data.audios.length > 0? (<div className="activity-content-audio-div">
          <audio ref={ref=>this.audio=ref} style={{display:"none"}} src={`${data.audios?data.audios[0].src : ""}`} onEnded={() => {
            this.setState({
              audioIsPause:true
            })
          }}></audio>
          <div className="activity-content-audio-title">{data.audios?data.audios[0].title : ""}</div>
          <div className="activity-content-audio-button" style={{background: `url(${this.state.audioIsPause?btn_play:btn_stop})   no-repeat  center / 100% 100%` }} onClick={()=>{
            if (this.state.audioIsPause) {
              this.audio.play();
              this.setState({
                audioIsPause:false
              })
            }else {
              this.audio.pause();
              this.setState({
                audioIsPause:true
              })
            }

          }}></div>
        </div>) : null}

        <div className="activity-content-content">
          <div className="activity-content-content-title"></div>
          <div className="activity-content-content-line"></div>
          <div className="activity-content-content-digest" dangerouslySetInnerHTML={{__html: data.content}}/>
        </div>
        <div className="activity-footer">
          <div className="bg-cc footer-image"></div>
        </div>
        <div className={`${data.time ? (data.time.start - (new Date().getTime()) < 0) ? "activity-footer-jieshu": (data.reservation.count ?  (data.reservation.count >= data.reservation.limit ? "activity-footer-man":"activity-footer-baoming") : "activity-footer-baoming"): ''}`} onClick={(e) => this.baoming(e.target.innerHTML)}>
        {data.time ? (data.time.start - (new Date().getTime()) < 0) ? "": (data.reservation.count ?  (data.reservation.count >= data.reservation.limit ? "":"") : ""): ''}
        </div>
      </div>
    );
  }
}

export default ActivityContent;



