import React, { Component } from 'react';
import './CancelActivity.css';

import { Activity, User } from '../api'
import { Toast } from 'antd-mobile';

const activityTypes = {
  adult:"",
  minor:"",
  "parent-child":""
}
const grades = ['','','','','','','','','','','','','','',''];

class CancelActivity extends Component {
  constructor(props){
      super(props);
      this.state = {
        activityContent: {},
        users:[],
        cancelBookedDisabled: false,

      };
  }

  timestampToTime = (timestamp) => {
        let date = new Date(timestamp);//10*1000131000
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
        let h = date.getHours() + ':';
        let m = date.getMinutes();
        // let s = date.getSeconds();
        let week = this.getweekDay(new Date(Y+M+D));
        let time = (h<=12) ? "" : "";
        return Y+M+D+"("+week+") "+time+h+m;
  }

  getweekDay = (date) => {
    let week;
    if(date.getDay()===0) week=""
    if(date.getDay()===1) week=""
    if(date.getDay()===2) week=""
    if(date.getDay()===3) week=""
    if(date.getDay()===4) week=""
    if(date.getDay()===5) week=""
    if(date.getDay()===6) week=""
    return week;
  }

  getGrade = (value) => {
    return grades[value];
  }

  async requestBookedData () {
    try{
      let data = await User.getMyBooked({
        _id : this.props.location.state.id,
      });
      if(data && data.code===true){
        this.setState({
          activityContent: data.data.activity,
          users: data.data.users
        });
      }
    }catch(e){
      //
    }
  }



  //
  async componentDidMount(){
    try{
      this.requestBookedData();

    }catch(e){
      //
    }
  }


  cancelBookedData = async () => {
    this.setState({
      cancelBookedDisabled: false
    });
    try{
      let data = await Activity.cancelBooked({
        _id: this.props.location.state.id,
      });

      Toast.success(data.msg, 2, () => {
        if (data.code === true) {

          this.props.history.goBack()

        }
      });

    }catch(e){
      //
    }
  }

  cancelBooked = () => {
    this.setState({
      cancelBookedDisabled: true
    })
    this.cancelBookedData();
  }

  mapper = (item, index) => { return (
    <div key={index} className="CancelActivity-canyuzhe-item ">{item.name}{item.type === 0 ? "" : ""}</div>
  )}

  render() {
    let data = this.state.activityContent || {};

    return(
      <div className="CancelActivity-home">
        <div className="CancelActivity-top">
          <div>{data.title}</div>
          <div style={{display:`${this.props.location.state.model === "lecture" ? "block" : "none"}`}}>{data.speaker}</div>
          <div>{data.time ? this.timestampToTime(data.time.start): ''}</div>
          <div>{data.address}</div>
          <div style={{display:`${this.props.location.state.model === "lecture" ? "none" : "block"}`}}>{activityTypes[data.type]}</div>
          <div>{this.props.location.state.model === "lecture" ? "" : ""}{data.limit? ((data.limit.type === 'age') ? (data.limit.age.min + "~" + data.limit.age.max + "") : (this.getGrade(data.limit.grade.min) + "~" + this.getGrade(data.limit.grade.max))) : ""}</div>
        </div>
        <div className="CancelActivity-lineStyle"></div>
        <div className="CancelActivity-canyuzhe"><span className="CancelActivity-canyuzhe-number">({this.state.users.length})</span></div>
        <div className="CancelActivity-canyuzhe-list">{this.state.users.map(this.mapper)}</div>
        <button className="CancelActivity-Button" onClick={this.cancelBooked} disabled={this.state.cancelBookedDisabled}></button>

      </div>
    );
  }
}

export default CancelActivity;



